import React from "react";
import { useParams, useNavigate } from "react-router-dom";

const translations = {
  hindi: { myProfile: " ", notLoggedIn: "    ,    ", goBack: " " },
  english: { myProfile: "My Profile", notLoggedIn: "You are not login please Login" , goBack: "Go Back"},
  punjabi: { myProfile: " ", notLoggedIn: "    ,     ", goBack: " " },
  malayalam: { myProfile: " ", notLoggedIn: "  ,   " , goBack: ""},
  telugu: { myProfile: " ", notLoggedIn: "  ,   " , goBack: " "},
  marathi: { myProfile: " ", notLoggedIn: "   ,   " , goBack: " "},
};

const Notloged = () => {
  const { language } = useParams();
  const navigate = useNavigate();
  const t = translations[language] || translations.english;

  const buttonStyle = {
    background: "none",
    border: "none",
    color: "white",
    fontSize: "0.8rem",
    cursor: "pointer",
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        height: "100vh",
        fontFamily: "Arial, sans-serif",
        backgroundImage: "url('/home1.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundBlendMode: "darken",
        backgroundColor: "rgba(0, 0, 0, 0.6)",
        color: "white",
      }}
    >
      {/* Horizontal Navbar */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          background: "#28a745",
          padding: "10px 20px",
          color: "white",
          flexWrap: "wrap",
          fontSize: "1.2rem",
        }}
      >
        <div style={{ fontSize: "1.5rem", fontWeight: "bold" }}>Krishi Agro</div>
        <div style={{ display: "flex", gap: "10px" }}>
          <button
            style={{
              background: "white",
              color: "#28a745",
              padding: "5px 10px",
              borderRadius: "5px",
              cursor: "pointer",
            }}
            onClick={() => navigate(`/login/${language}`)}
          >
            Login
        
          </button>
          <div>
            <button style={{ background: "none", border: "none", color: "white", fontSize: "1rem", cursor: "pointer" }} onClick={() => navigate(`/home1/${language}`)}>
            {t.goBack}
          </button>
          </div>
        </div>
      </div>

      {/* Main Layout */}
      <div
        style={{ display: "flex", flex: 1, flexDirection: "row", overflow: "hidden" }}
      >
        {/* Vertical Navbar */}
        <div
        
          style={{
            width: "100%",
            maxWidth: "120px",
            background: "#333",
            color: "white",
            padding: "10px",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            fontSize: "0.8rem",
            minWidth: "60px",
            justifyContent: "space-between",
          }}
        
        >
            
          <div style={{ display: "flex", flexDirection: "column", alignItems: "center", gap: "10px" }}>
            <div style={{ fontSize: "1.2rem", marginBottom: "10px" }}></div>
            <button style={buttonStyle} onClick={() => navigate(`/businessman/notloge/${language}`)}>
              {t.myProfile}
            </button>
            
         
        
          </div>
          <div></div> {/* empty for bottom spacing */}
        </div>

        {/* Middle Content */}
        <div
          style={{
            flex: 1,
            overflowY: "auto",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            fontSize: "1.2rem",
            color: "white",
            padding: "20px",
          }}
        >
          <div
            style={{
              backgroundColor: "rgba(255,255,255,0.1)",
              padding: "30px 50px",
              borderRadius: "10px",
              backdropFilter: "blur(5px)",
              boxShadow: "0 0 10px rgba(0,0,0,0.5)",
              textAlign: "center",
              color: "white",
            }}
          >
            {t.notLoggedIn}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Notloged;
